.map-container .circle {
    background: rgba(0, 47, 255, 0.658);
    opacity: 1;
    border-radius: 100%;
    min-width: 20px;
    min-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
}

.map-container .currentlocation {
    background-color: rgba(0, 255, 106, 0.658);
    cursor: default;
}

.map-container .info {
    position: fixed;
    top: 100px;
    left: 2vw;
    background-color: white;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
    border-radius: 5px;
}

.map-container .heat-map {
    width: 20vw;
    /* font: 16px "Lucida Grande", "Helvetica", "Arial", sans-serif; */
  }

.map-container .heat-map .td, .heat-map .tr {
    width: 5px;
}
.map-container .detail {
    text-align: center;
}

/* Basic Alignment */

.align-row {
    display: flex;
    flex-direction: row;
}

.align-column {
    display: flex;
    flex-direction: column;
}

.space-between {
    display: flex;
    justify-content: space-between;
}

.space-around {
    display: flex;
    justify-content: space-around;
}

.flex-center {
    display: flex;
    justify-content: center;
}