.Home .lander {
  padding: 80px 0;
  text-align: center;
}

.Home .lander h1 {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}

.Home .lander p {
  color: #999;
}

.Home .googlemap{
  width: auto
}

.Home .heat-map-vertical{
  display: block;
}

.Home .heat-map{
  display: none;
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .Home .heat-map-vertical{
    display: none;
  }
  .Home .heat-map{
    display: block;
  }
}
